import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { deleteItemCart, inDeCreaseFunc } from "../actions";
import { getProvin, getDistricByParentCode, numberToVnd } from "../common";
import "./Payment.css";
import Select from "react-select";

class Payment extends Component {
  state = {
    province: undefined,
    distric: undefined,
  };
  handleChangeProvince = (selectedOption) => {
    this.setState({
      ...this.state,
      province: selectedOption,
    });
  };
  handleChangeDistric = (selectedOption) => {
    this.setState({
      ...this.state,
      distric: selectedOption,
    });
  };
  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col-12 col-sm-12 col-md-8">
            <h5 style={{ marginTop: "30px", paddingTop: "10px" }}>
              THNG TIN N HNG
            </h5>
            <table className="table table-hover text-center">
              <thead>
                <tr>
                  <th scope="col">Hnh</th>
                  <th scope="col">Sn phm</th>
                  <th scope="col">SL</th>
                  <th scope="col">n Gi</th>
                  <th scope="col">Size</th>
                </tr>
              </thead>
              <tbody>
                {this.props.carts &&
                  this.props.carts.map((item, index) => (
                    <>
                      <tr key={index}>
                        <td>
                          <img
                            src={item.product.jsonImg[0]}
                            alt={item.product.name}
                          ></img>
                        </td>
                        <td>{item.product.name}</td>
                        <td>
                          {/* <i
                          className="fas fa-minus-square mr-1"
                          onClick={() => {
                            this.props.inDeCreaseFunc(item.product, "DE");
                          }}
                        ></i>
                        {item.quanty}
                        <i
                          className="fas fa-plus-square ml-1"
                          onClick={() => {
                            this.props.inDeCreaseFunc(item.product, "IN");
                          }}
                        ></i> */}
                          <span
                            onClick={() =>
                              this.props.inDeCreaseFunc(item.product, "IN")
                            }
                          >
                            <i className="fas fa-plus"> </i>
                          </span>
                          <span className="mx-2">{item.quanty}</span>
                          <span
                            onClick={() =>
                              this.props.inDeCreaseFunc(item.product, "DE")
                            }
                          >
                            <i className="fas fa-minus"> </i>
                          </span>
                        </td>
                        <td>{item.product.nowPrice}</td>
                        <td>{item.size}</td>
                      </tr>
                      <tr>
                        <td>Tng</td>
                        <td>
                          {numberToVnd(item.product.nowPrice * item.quanty)}
                        </td>
                        <td>Xa</td>
                        <td>
                          <i
                            className="edit fa fa-trash"
                            onClick={() =>
                              this.props.deleteItemCart(item.product)
                            }
                          ></i>
                        </td>
                      </tr>
                    </>
                  ))}
              </tbody>
            </table>
            <div>
              <hr />
              <div className="payment-totalprice-container">
                <p>Tng tin</p>
                <p>{numberToVnd(this.props.totalPrice)}</p>
              </div>
              <div className="d-flex flex-row-reverse bd-highlight">
                <button form="my-form" type="submit" class="btn btn-danger">
                  Mua hng
                </button>
              </div>
            </div>
          </div>
          <div className="col-sm-12 col-md-4">
            <form
              id="my-form"
              onSubmit={() => alert("Hin Admin bun nn khng ngh khng bn")}
            >
              <h5>THNG TIN KHCH HNG</h5>
              <div class="form-group">
                <label for="exampleInputEmail1">H v tn *</label>
                <input
                  type="text"
                  className="form-control"
                  placeholder="VD: o Thng"
                  required="required"
                  pattern="[A-Za-z]+"
                />
              </div>
              <div class="form-group">
                <label for="exampleInputEmail1">Email</label>
                <input
                  type="email"
                  class="form-control"
                  aria-describedby="emailHelp"
                  placeholder="VD: daothang@email.com"
                />
              </div>
              <div class="form-group">
                <label for="exampleInputEmail1">S in thoi*</label>
                <input
                  type="text"
                  class="form-control"
                  placeholder="VD: 098999888"
                  required="required"
                  pattern="[0-9]+"
                />
              </div>
              <h5>A CH GIAO HNG</h5>
              <div className="form-group">
                <label for="exampleInputPassword4">Chn Tnh / Thnh Ph</label>
                <Select
                  styles={{
                    // Fixes the overlapping problem of the component
                    menu: (provided) => ({ ...provided, zIndex: 9999 }),
                  }}
                  isSearchable
                  value={this.state.province}
                  onChange={this.handleChangeProvince}
                  options={getProvin()}
                />
              </div>
              <div className="form-group">
                <label for="exampleInputPassword4">Chn Qun / Huyn</label>
                <Select
                  styles={{
                    // Fixes the overlapping problem of the component
                    menu: (provided) => ({ ...provided, zIndex: 9999 }),
                  }}
                  isSearchable
                  isLoading={false}
                  value={this.state.distric}
                  onChange={this.handleChangeDistric}
                  options={
                    this.state.province &&
                    getDistricByParentCode(this.state.province.code)
                  }
                />
              </div>
              <div class="form-group">
                <label for="exampleInputEmail1">Tn phng x*</label>
                <input type="text" className="form-control" />
              </div>
              <div class="form-group">
                <label for="exampleInputEmail1">S nh tn ng*</label>
                <input type="text" className="form-control" />
              </div>
              <div class="form-group">
                <label for="exampleInputEmail1">Ghi ch</label>
                <input
                  type="text"
                  className="form-control"
                  style={{ height: "50px" }}
                />
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

Payment.defaultProps = {
  statusAction: -1,
  carts: [],
  totalPrice: 0,
};
//tao component ProductCard, dua component vao list

const mapStateToProps = (state) => ({
  statusAction: state.statusAction,
  carts: state.carts,
  totalPrice: state.totalPrice,
});

const mapDispatchToProps = (dispatch) => ({
  ...bindActionCreators(
    {
      deleteItemCart,
      inDeCreaseFunc,
    },
    dispatch
  ),
});
export default connect(mapStateToProps, mapDispatchToProps)(Payment);
